@import url(https://fonts.googleapis.com/css?family=Roboto:regular,bold&subset=latin,latin-ext);
.listItem {
  display: flex;
  padding: 7px;
  border: 1px solid transparent; }
  .listItem:not(:last-of-type) {
    border-bottom: 1px solid #73df72; }
  .listItem button {
    cursor: pointer;
    background: none;
    border: none;
    font-size: 20px;
    outline: none;
    color: gray;
    transition: color .25s ease-in-out; }
    .listItem button:hover {
      color: black; }
  .listItem [type='checkbox'] {
    align-self: center;
    margin-right: 5px; }
  .listItem [type='text'] {
    padding: 2px;
    font-size: 16px;
    border-color: transparent;
    border-bottom-color: #f5f5f5;
    outline: none; }
  .listItem p,
  .listItem .editTextInput {
    flex-grow: 1;
    align-self: center; }
  .listItem p {
    padding-left: 5px;
    padding-top: 2px;
    font-size: 16px; }

body {
  font-family: Roboto, sans-serif !important; }

.app {
  width: 50%;
  margin: 0 auto;
  margin-top: 100px; }
  .app h3 {
    font-size: 20px; }
  .app form {
    margin-top: 50px; }
    .app form input,
    .app form button {
      padding: 5px;
      font-size: 16px;
      border: 2px #73df72 solid;
      outline: none; }
    .app form button {
      background: #73df72;
      border-left: none;
      color: white; }

